main {
    padding: 0 15px;
}
.form_btn {
    display: inline-block;
    background-color: #a12d78;
    background-image: linear-gradient(-51deg, rgba(221, 0, 74, 0.93) 0%, rgba(254, 143, 98, 0.93) 100%);
    border: 2px solid #fff;
    padding: 10px 35px;
    color: #fff;
    text-align: center;
    border-radius: 25px;
    transition: .3s all;
    &:hover {
        color: #a12d78;
        background-image: none;
        background-color: #fff;
        border: 2px solid #a12d78;
    }
}

.h2-title {
    font-family: 'Montserrat', sans-serif;
    color: #3366FF;
    font-size: 42px;
    line-height: 1.5;
    position: relative;
    font-weight: 700;
    text-transform: uppercase;
    .sm-block({
        text-align: center;
        font-size: 36px;
    });
    .xs-block({
        text-align: center;
        font-size: 32px;
        line-height: 1.2;
    });
}
.subtitle {
    color: #36f;
    font-weight: 700;
    text-transform: uppercase;
    .sm-block({
        text-align: center;
    });
}
.freak {
    .move_item__right {
        .sm-block({
            order: 2;
        });
    }
    .move_item__left {
        .sm-block({
            order: 1;
        });
    }
}
.header {
    box-shadow: 0px 1px 1px 0px rgba(0,0,0,0.2);
    padding: 15px 0;
    &_wrap {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: center;
        min-height: 40px;
        .xs-block({
            text-align: center;
            justify-content: center;
        });
    }
    &_item {
        margin: 5px 20px;
        .xs-block({
            margin: 5px 10px;
        });
    }
    &_logo {
        font-family: 'Pacifico', cursive;
        margin-left: 0;
        position: relative;
    }
    &_tel {
        position: relative;
        padding-left: 25px;
        padding-bottom: 5px;
        &::after {
            content: '';
            position: absolute;
            left: 0; top: 0;
            background: url(../img/tel.png) no-repeat;
            width: 17px;
            height: 17px;
            transition: .3s all;
        }
        &:hover {
            text-decoration: underline;
            &::after {
                transform: rotate(30deg);
            }
        }
    }
    &_mail {
        color: #0000FF;
        text-decoration: underline;
        &:hover {
            text-decoration: none;
        }
    }
}
.move {
    margin-top: 110px;
    .sm-block({
        margin-top: 50px;
    });
    &_title {
        &::after {
            content: 'Наши направления';
            position: absolute;
            color: #f7f7f7;
            font-size: 64px;
            top: -48px; left: 0;
            z-index: -1;
            .sm-block({
                content: '';
                display: none;
            });
        }
    }
    &_item {
        margin: 80px 0;
        .sm-block({
            margin: 50px 0;
            justify-content: center;
        });
        .row-flex();
        &__left {
            .col();
            .size(4);
            .size-sm(10);
            .size-xxs(11);
            text-align: center;
        }
        &__right {
            .col();
            .size(8);
            .size-sm(10);
            .size-xxs(11);
            padding: 0 50px;
            line-height: 1.5;
            .sm-block({
                padding: 0 20px;
                margin: 25px 0;
            });
            .xs-block({
                padding: 0px;
            });
            
        }
        &--title {
            display: inline-block;
            font-size: 20px;
            color: #36f;
            font-weight: 700;
            max-width: 350px;
            margin: 0 auto;
        }
        &--img {
            margin: 25px auto 35px;
        }
        &--subtitle {
            margin-top: 25px;
        }
        &--ul {
            display: block;
            margin-top: 15px;
            margin-left: 5px;
        }
        &--li {
            padding-left: 30px;
            position: relative;
            margin: 5px 0;
            &::after {
                content: '';
                position: absolute;
                top: 5px; left: 0;
                background: url(../img/check-mark.png) no-repeat;
                width: 16px;
                height: 16px;
            }
        }
    }
}
.hide {
    height: 10px;
    overflow-y: hidden;
}
.click {
    margin-top: 15px;
    cursor: pointer;
    padding: 5px 15px;
    background: #eee;
    color: #999;
    border-radius: 25px;
    position: relative;
    padding-right: 25px;
    &::after {
        content: '';
        position: absolute;
        top: 12px; right: 10px;
        width: 0; 
        height: 0; 
        border-left: 3px solid transparent;
        border-right: 3px solid transparent;
        border-top: 3px solid #3d3d3d;
    }
}

.spons {
    padding-top: 25px;
    .sm-block({
        padding-top: 0;
    });
    &_title {
        &::after {
            content: 'Партнеры проекта';
            position: absolute;
            color: #f7f7f7;
            font-size: 64px;
            top: -48px; left: 0;
            z-index: -1;
            .sm-block({
                content: '';
                display: none;
            });
        }
    }
    &_wrap {
        margin-top: 55px;
        padding: 0 40px;
        position: relative;
    }
}
.slick-arrow {
    cursor: pointer;
    font-size: 0;
    padding: 20px;
    position: absolute;
    
}
.slick-prev {
    background: url(../img/left-arrow.png) no-repeat;
    width: 32px;
    height: 32px;
    top: 50%; left: 0;
    transform: translate(0,-50%);
}
.slick-next {
    background: url(../img/right-arrow.png) no-repeat;
    width: 32px;
    height: 32px;
    top: 50%; right: 0;
    transform: translate(0,-50%);
}
.gallery {
    margin-top: 110px;
    .sm-block({
        margin-top: 55px;
    });
    &_title {
        text-align: right;
        &::after {
            content: 'Галерея';
            position: absolute;
            color: #f7f7f7;
            font-size: 64px;
            top: -48px; right: 0;
            z-index: -1;
            .sm-block({
                content: '';
                display: none;
            });
        }
    }
    &_wrap {
        margin-top: 35px;
        .row-flex();
        justify-content: center;
    }
    &_item {
        .col();
        .size(2.5);
        .size-md(3.5);
        .size-sm(5);
        .size-xs(10);
        margin: 20px;
    }
}
.mfp-close {
    cursor: pointer !important;
}
.footer {
    padding: 20px 0;
    margin-top: 50px;
    border: 1px solid #000;
    background-color: #292929;
    color: #fff;
}