@import "smart-grid.less";
@import "font.less";
@import "index.less";
.reset();
.container {
    .container();
}
html {
    font-size: 16px;
}
a {
    text-decoration: none;
    color: inherit;
}
a:hover {
    text-decoration: none;
}
body {
    font-family: 'Proxima Nova Rg';
    font-weight: normal;
    font-style: normal;
}
ul,li {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
:active, :hover, :focus {
    outline: 0;
    outline-offset: 0;
}
input {
    border: none;
}
button {
    background: none;
    border: none;
}
* {
    box-sizing: border-box;
}