@font-face {
    font-family: 'Proxima Nova Rg';
    src: url('../fonts/ProximaNova-Regular.eot');
    src: local('Proxima Nova Regular'), local('ProximaNova-Regular'),
        url('../fonts/ProximaNova-Regular.eot?#iefix') format('embedded-opentype'),
        url('../fonts/ProximaNova-Regular.woff') format('woff'),
        url('../fonts/ProximaNova-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Proxima Nova Lt';
    src: url('../fonts/ProximaNova-Semibold.eot');
    src: local('Proxima Nova Semibold'), local('ProximaNova-Semibold'),
        url('../fonts/ProximaNova-Semibold.eot?#iefix') format('embedded-opentype'),
        url('../fonts/ProximaNova-Semibold.woff') format('woff'),
        url('../fonts/ProximaNova-Semibold.ttf') format('truetype');
    font-weight: 600;
    font-style: normal;
}
@font-face {
    font-family: 'Moloko';
    src: url('../fonts/cyrillic_font_moloko_by_wakabaru.eot');
    src: url('../fonts/cyrillic_font_moloko_by_wakabaru.eot') format('embedded-opentype'),
        url('../fonts/cyrillic_font_moloko_by_wakabaru.woff2') format('woff2'),
        url('../fonts/cyrillic_font_moloko_by_wakabaru.woff') format('woff'),
        url('../fonts/cyrillic_font_moloko_by_wakabaru.ttf') format('truetype'),
        url('../fonts/cyrillic_font_moloko_by_wakabaru.svg#cyrillic_font_moloko_by_wakabaru') format('svg');
}
.reg {
    font-family: 'Proxima Nova Rg';
    font-weight: normal;
    font-style: normal;
}
.semi {
    font-family: 'Proxima Nova Lt';
    font-weight: 600;
    font-style: normal;
}