@font-face {
  font-family: 'Proxima Nova Rg';
  src: url('../fonts/ProximaNova-Regular.eot');
  src: local('Proxima Nova Regular'), local('ProximaNova-Regular'), url('../fonts/ProximaNova-Regular.eot?#iefix') format('embedded-opentype'), url('../fonts/ProximaNova-Regular.woff') format('woff'), url('../fonts/ProximaNova-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Proxima Nova Lt';
  src: url('../fonts/ProximaNova-Semibold.eot');
  src: local('Proxima Nova Semibold'), local('ProximaNova-Semibold'), url('../fonts/ProximaNova-Semibold.eot?#iefix') format('embedded-opentype'), url('../fonts/ProximaNova-Semibold.woff') format('woff'), url('../fonts/ProximaNova-Semibold.ttf') format('truetype');
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: 'Moloko';
  src: url('../fonts/cyrillic_font_moloko_by_wakabaru.eot');
  src: url('../fonts/cyrillic_font_moloko_by_wakabaru.eot') format('embedded-opentype'), url('../fonts/cyrillic_font_moloko_by_wakabaru.woff2') format('woff2'), url('../fonts/cyrillic_font_moloko_by_wakabaru.woff') format('woff'), url('../fonts/cyrillic_font_moloko_by_wakabaru.ttf') format('truetype'), url('../fonts/cyrillic_font_moloko_by_wakabaru.svg#cyrillic_font_moloko_by_wakabaru') format('svg');
}

.reg {
  font-family: 'Proxima Nova Rg';
  font-weight: normal;
  font-style: normal;
}

.semi {
  font-family: 'Proxima Nova Lt';
  font-weight: 600;
  font-style: normal;
}

main {
  padding: 0 15px;
}

.form_btn {
  display: inline-block;
  background-color: #a12d78;
  background-image: linear-gradient(-51deg, rgba(221, 0, 74, 0.93) 0%, rgba(254, 143, 98, 0.93) 100%);
  border: 2px solid #fff;
  padding: 10px 35px;
  color: #fff;
  text-align: center;
  border-radius: 25px;
  transition: .3s all;
}

.form_btn:hover {
  color: #a12d78;
  background-image: none;
  background-color: #fff;
  border: 2px solid #a12d78;
}

.h2-title {
  font-family: 'Montserrat', sans-serif;
  color: #3366FF;
  font-size: 42px;
  line-height: 1.5;
  position: relative;
  font-weight: 700;
  text-transform: uppercase;
}

.subtitle {
  color: #36f;
  font-weight: 700;
  text-transform: uppercase;
}

.header {
  box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.2);
  padding: 15px 0;
}

.header_wrap {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  min-height: 40px;
}

.header_item {
  margin: 5px 20px;
}

.header_logo {
  font-family: 'Pacifico', cursive;
  margin-left: 0;
  position: relative;
}

.header_tel {
  position: relative;
  padding-left: 25px;
  padding-bottom: 5px;
}

.header_tel::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  background: url(../img/tel.png) no-repeat;
  width: 17px;
  height: 17px;
  transition: .3s all;
}

.header_tel:hover {
  text-decoration: underline;
}

.header_tel:hover::after {
  transform: rotate(30deg);
}

.header_mail {
  color: #0000FF;
  text-decoration: underline;
}

.header_mail:hover {
  text-decoration: none;
}

.move {
  margin-top: 110px;
}

.move_title::after {
  content: 'Наши направления';
  position: absolute;
  color: #f7f7f7;
  font-size: 64px;
  top: -48px;
  left: 0;
  z-index: -1;
}

.move_item {
  margin: 80px 0;
  display: flex;
  flex-wrap: wrap;
  margin-left: -7.5px;
  margin-right: -7.5px;
}

.move_item__left {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 7.5px;
  margin-right: 7.5px;
  width: calc(33.333333333333336% - 15px);
  text-align: center;
}

.move_item__right {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 7.5px;
  margin-right: 7.5px;
  width: calc(66.66666666666667% - 15px);
  padding: 0 50px;
  line-height: 1.5;
}

.move_item--title {
  display: inline-block;
  font-size: 20px;
  color: #36f;
  font-weight: 700;
  max-width: 350px;
  margin: 0 auto;
}

.move_item--img {
  margin: 25px auto 35px;
}

.move_item--subtitle {
  margin-top: 25px;
}

.move_item--ul {
  display: block;
  margin-top: 15px;
  margin-left: 5px;
}

.move_item--li {
  padding-left: 30px;
  position: relative;
  margin: 5px 0;
}

.move_item--li::after {
  content: '';
  position: absolute;
  top: 5px;
  left: 0;
  background: url(../img/check-mark.png) no-repeat;
  width: 16px;
  height: 16px;
}

.hide {
  height: 10px;
  overflow-y: hidden;
}

.click {
  margin-top: 15px;
  cursor: pointer;
  padding: 5px 15px;
  background: #eee;
  color: #999;
  border-radius: 25px;
  position: relative;
  padding-right: 25px;
}

.click::after {
  content: '';
  position: absolute;
  top: 12px;
  right: 10px;
  width: 0;
  height: 0;
  border-left: 3px solid transparent;
  border-right: 3px solid transparent;
  border-top: 3px solid #3d3d3d;
}

.spons {
  padding-top: 25px;
}

.spons_title::after {
  content: 'Партнеры проекта';
  position: absolute;
  color: #f7f7f7;
  font-size: 64px;
  top: -48px;
  left: 0;
  z-index: -1;
}

.spons_wrap {
  margin-top: 55px;
  padding: 0 40px;
  position: relative;
}

.slick-arrow {
  cursor: pointer;
  font-size: 0;
  padding: 20px;
  position: absolute;
}

.slick-prev {
  background: url(../img/left-arrow.png) no-repeat;
  width: 32px;
  height: 32px;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
}

.slick-next {
  background: url(../img/right-arrow.png) no-repeat;
  width: 32px;
  height: 32px;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
}

.gallery {
  margin-top: 110px;
}

.gallery_title {
  text-align: right;
}

.gallery_title::after {
  content: 'Галерея';
  position: absolute;
  color: #f7f7f7;
  font-size: 64px;
  top: -48px;
  right: 0;
  z-index: -1;
}

.gallery_wrap {
  margin-top: 35px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -7.5px;
  margin-right: -7.5px;
  justify-content: center;
}

.gallery_item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 7.5px;
  margin-right: 7.5px;
  width: calc(20.833333333333336% - 15px);
  margin: 20px;
}

.mfp-close {
  cursor: pointer !important;
}

.footer {
  padding: 20px 0;
  margin-top: 50px;
  border: 1px solid #000;
  background-color: #292929;
  color: #fff;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

html {
  font-size: 16px;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

body {
  font-family: 'Proxima Nova Rg';
  font-weight: normal;
  font-style: normal;
}

ul,
li {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

input {
  border: none;
}

button {
  background: none;
  border: none;
}

* {
  box-sizing: border-box;
}

@media screen and (max-width: 1200px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 992px) {
  .gallery_item {
    width: calc(29.166666666666668% - 15px);
  }

  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 768px) {
  .h2-title {
    text-align: center;
    font-size: 36px;
  }

  .subtitle {
    text-align: center;
  }

  .freak .move_item__right {
    order: 2;
  }

  .freak .move_item__left {
    order: 1;
  }

  .move {
    margin-top: 50px;
  }

  .move_title::after {
    content: '';
    display: none;
  }

  .move_item {
    margin: 50px 0;
    justify-content: center;
  }

  .move_item__left {
    width: calc(83.33333333333334% - 15px);
  }

  .move_item__right {
    width: calc(83.33333333333334% - 15px);
  }

  .move_item__right {
    padding: 0 20px;
    margin: 25px 0;
  }

  .spons {
    padding-top: 0;
  }

  .spons_title::after {
    content: '';
    display: none;
  }

  .gallery {
    margin-top: 55px;
  }

  .gallery_title::after {
    content: '';
    display: none;
  }

  .gallery_item {
    width: calc(41.66666666666667% - 15px);
  }

  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 480px) {
  .h2-title {
    text-align: center;
    font-size: 32px;
    line-height: 1.2;
  }

  .header_wrap {
    text-align: center;
    justify-content: center;
  }

  .header_item {
    margin: 5px 10px;
  }

  .move_item__right {
    padding: 0px;
  }

  .gallery_item {
    width: calc(83.33333333333334% - 15px);
  }

  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 360px) {
  .move_item__left {
    width: calc(91.66666666666667% - 15px);
  }

  .move_item__right {
    width: calc(91.66666666666667% - 15px);
  }

  .container {
    padding-left: 5px;
    padding-right: 5px;
  }
}